import styled from "styled-components";

import { colors, fontFamily, widthSizes } from "../../styleguide";

import { LegacyButtonVariants } from "./legacyButtonVariants";
import { returnVariant } from "./utils";

export interface StyledButtonProps {
	variant?: LegacyButtonVariants
	rightAlignIcon?: boolean
	widthSize?: string
	color?: string
	backgroundColor?: string
	borderColor?: string
	disabledBackgroundColor?: string
	disabledTextColor?: string
	hoverBackgroundColor?: string
	hoverTextColor?: string
	mt?: string
	mb?: string
	ml?: string
	mr?: string
}

export const StyledButton = styled.button<StyledButtonProps>`
	height: 40.5px;
	width: ${({ widthSize }) => widthSize};

	color: ${({ variant }) => variant && returnVariant(variant).color};
	background-color: ${({ variant }) => variant && returnVariant(variant).backgroundColor};
	border: ${({ variant }) => variant && returnVariant(variant).border};

	box-sizing: border-box;
	align-items: center;
	display: flex;

	flex-direction: ${({ rightAlignIcon }) => rightAlignIcon ? "row-reverse" : "row"};
	justify-content: center;
	position: relative;
	cursor: pointer;

	border-radius: 24px;
	font-size: 13px;
	font-weight: 700;
	line-height: 1.1;

	text-align: center;
	text-decoration: none;
	white-space: nowrap;

	padding: ${({ widthSize }) => (widthSize === widthSizes.icon ? "0px" : " 0px 16px")};

	${(props) => props.mt && { marginTop: `${props.mt}` }}
	${(props) => props.mb && { marginBottom: `${props.mb}` }}
	${(props) => props.mr && { marginRight: `${props.mr}` }}
	${(props) => props.ml && { marginLeft: `${props.ml}` }}

	font-family: ${fontFamily.larsseit};
	font-style: normal;

	&:hover {
		background-color: ${({ variant }) => variant && returnVariant(variant).hover};
		color: ${({ variant }) => variant && returnVariant(variant).hoverTextColor};
		svg {
			stroke: ${({ variant }) => variant && returnVariant(variant).hoverTextColor};
		}
		${(props) => props.variant === "custom" && ({
		color: props.hoverTextColor,
		backgroundColor: props.hoverBackgroundColor,
	})}
	};

	&:active,
	&:visited {
		background-color: ${({ variant }) => variant && returnVariant(variant).visited};
	};

	&:disabled {
		background-color: ${({ variant }) => variant && returnVariant(variant).disabled};
		color: ${({ variant }) => variant && returnVariant(variant).disabledText};
		border: ${({ variant }) => variant && returnVariant(variant).disabledBorder};
		svg {
			stroke: ${({ variant }) => variant && returnVariant(variant).disabledText};
		}
		&:hover {
			cursor: not-allowed;
			svg {
				stroke: ${({ variant }) => variant && returnVariant(variant).disabledText};
			}
		};
		${(props) => props.variant === "custom" && ({
		color: props.disabledTextColor,
		backgroundColor: props.disabledBackgroundColor,
	})}
	};

	&:focus {
		outline-offset: 2px;
		outline: 3px solid ${colors.midBlue}33;
	};

	${(props) => props.variant === "custom" && ({
		color: props.color,
		backgroundColor: props.backgroundColor,
		border: `1px solid ${props.borderColor || "black"}`,
	})}
`;

